<template lang="en">
<div>
    <div class="my-popups">
        <div class="modal d-block ">

            <div class="modal-dialog ui-draggable" style="display: inline-block;">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle">
                        <h5 id="ui-id-26" class="modal-title">{{$t('Communication')}}</h5>
                        <button type="button" title="" @click="$emit('close')" class="close" data-original-title="Close">
                            <i class="fa fa-times" aria-hidden="true"></i>
                        </button>
                        <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG" role="button"
                                aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('CommunicationGuide')"></div>
                                </div>
                            </div>
                        </span>
                    </div>

                    <div id="backgroundPopup" class="modal-body" style="height: auto; min-height: 0px; max-height: none;">
                        <section id="main-content" class="main-content">
                            <section class="main-content">
                                <div class="container-fluid p-0">
                                    <div class="p-0">
                                        <dynamic-form ref="addCommunication" :schema="FormSchema" :buttons="buttons" v-on:OnSubmit="onSubmit">
                                            <template v-slot:tgslot-ScreenName="{ data }">
                                                <div class="input-group">
                                                    <input type="text" v-model="data.fieldInfo.value" :class="{'form-control w-100':true,'is-invalid':data.error.length !=''}" :name="ScreenName">
                                                    <span v-if="data.error.length !=''" name="ScreenName" class="invalid-feedback">{{data.error}}</span>
                                                    <!--  v-on:keyup="onKeyUp(data)" <span v-if="(typeof(data.fieldInfo.validationRules.error) !='undefined' && data.fieldInfo.validationRules.error !='')" name="ScreenName" class="invalid-feedback">{{data.fieldInfo.error}}</span> -->
                                                </div>
                                            </template>
                                            <!-- <template v-slot:tgslot-Content="{ data }">
                                                <ckeditor :editor="editor" @ready="onReady" v-model="editorData" ref="textarea"
                                                    :class="{ 'is-invalid': data.error != '' }"></ckeditor>
                                                <span class="invalid-feedback">{{ data.error }}</span>
                                            </template> -->
                                        </dynamic-form>
                                        <!-- <dynamic-form ref="dynamicForm" class="daterangecontrolHS" lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                                        </dynamic-form> -->
                                    </div>
                                </div>
                            </section>
                        </section>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<style>
.singleselectlook .multiselect__select::before {
    z-index: 99;
}

.singleselectlook .multiselect__tags {
    padding: 8px 30px 0 8px !important;
    min-height: 36px !important;
}

.singleselectlook .multiselect__input {
    margin-bottom: 0px;
}
</style>

<script>
import {
    defineComponent
} from '@vue/composition-api'
import {
    extend,
    ValidationObserver,
    ValidationProvider
} from 'vee-validate';
import {
    ConvertCustomFieldIntoDynamicFormObj,
    ConvertDynamicFormObjToCustomFieldObj
} from "@/assets/common/vue-common.js"
import DataService from '../../services/DataService';
extend('required', {
    validate(value) {
        console.log("checkk")
        return {
            required: true,
            valid: ['', null, undefined].indexOf(value) === -1
        };
    },
    message: 'The {_field_} is required'
    ///computesRequired: true
});

export default defineComponent({
    name: "Manage Departemts",
    components: {
        ValidationObserver,
        ValidationProvider
    },
    props: {
        Userid: {
            type: Number
        },
        Items: {
            type: Array
        },
        addApiUrl: {
            type: String
        },
        emitName: {
            type: String
        },
        ModuleName: {
            type: String
        },
        SubModule: {
            type: String
        },
        title: {
            type: String
        },
        recordId: {
            type: Number
        }
    },
    data() {
        return {
            editor: this.CLASSIC_EDITOR,
            // department: {
            //     name: '',
            //     status: {
            //         name: "active",
            //         id: 1
            //     },
            //     visibaleToclients: false,
            //     visibaleToTicting: false,
            //     description: '',
            // },
            FormSchema: [{
                    layoutType: "double",
                    Data: [{
                            astype: "SelectField",
                            label: this.$t('CommunicationType'),
                            name: "CommunicationType",
                            value: "",
                            placeholder: "Communication Type",
                            config: {
                                options: [],
                                //onChange: this.GetSubCategoryDDL
                            },
                            validationRules: {
                                "required": true
                            },
                        },
                        // {
                        //     astype: "TextField",
                        //     label: this.$t('Address/Screen Name'),
                        //     name: "Address/Screen Name",
                        //     value: "",
                        //     placeholder: "Address/Screen Name",
                        //     validationRules: {"required": true},                            
                        //     on:this.onKeyUp,
                        //     disabled: false,
                        // }, 
                        {
                            astype: "SlotField",
                            label: this.$t('Address/Screen Name'),
                            name: "ScreenName",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                "required": true
                            },
                        },

                    ],
                },
                {
                    layoutType: "single",
                    Data: [{
                            astype: "TextAreaField",
                            label: this.$t('Note/Description'),
                            name: "Description",
                            value: "",
                            placeholder: this.$t('Note/Description'),
                            validationRules: "",
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('Visible To Client'),
                            name: "is_visible",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                        },

                    ]
                },
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closePopup,
                },
            ],
            TypeData: [],
            emailValidate: {},
            emailValidateemails: {},
            isLoading: false
        }

    },

    created: function () {        
        var vm = this;
        vm.GetEmailTypes();
        if (vm.Items.emailId > 0) {
            vm.FormSchema[0].Data[0].value = vm.Items.typeId;
            vm.FormSchema[0].Data[1].value = vm.Items.screenName;
            vm.FormSchema[1].Data[0].value = vm.Items.userEmailDesc;
            vm.FormSchema[1].Data[1].value = vm.Items.isVisibleToClient;
        }

        setTimeout(() => {
            this.$refs.addCommunication.UpdateKeyValue();
        }, 200);
        // let id = this.recordId ? this.recordId : 0
        // this.getForm(id)

    },
    methods: {
        getForm: function (id) {
            var vm = this;
            vm.isLoading = true;
            var url = `recordId=${id}&moduleName=${vm.ModuleName}&subModuleCode=${vm.SubModule}`;
            DataService.GetDynamicForm(url).then(function (response) {
                if (response != null && response.data != null) {
                    vm.isMapFeature = response.data.isFsmLocating;
                    response.data.data
                    vm.FormSchema = ConvertCustomFieldIntoDynamicFormObj(response.data.data)
                    console.log("tis", vm.FormSchema)
                    if (vm.FormSchema.length > 0) {
                        vm.FormSchema[0].visibility = false
                    }
                    vm.isLoading = false;
                }
            });
        },

        async GetEmailTypes() {
            var vm = this;
            vm.isLoading = true;
            await DataService.GetEmailTypes().then((response) => {
                if (JSON.parse(response.data) != null) {
                    vm.TypeData = [];
                    vm.TypeData = JSON.parse(response.data);
                    vm.TypeData.forEach((itm, index) => {
                        (vm.FormSchema)[0].Data[0].config.options.push({
                            name: itm.CARRIER_TYPE_NAME,
                            value: itm.CARRIER_TYPE_ID,
                        })
                    })
                    vm.isLoading = false;
                }
                vm.isLoading = false;
            });
        },

        async onSubmit() {
            let obj = ConvertDynamicFormObjToCustomFieldObj(this.FormSchema)
            DataService.DepartmentAdd(obj, this.addApiUrl).then((res) => {
                if (res.data.Result != null && res.data.Result.MAIN_ID > 0) {
                    this.ShowAlert(this.$t('SavedSuccessfully'), "success", false, this.$t("Alert"));
                    this.$emit(this.emitName)
                } else {
                    this.ShowAlert(this.$t(res.data.Status), "danger", false, this.$t("Alert"));
                }

            })
        },
        closePopup() {
            this.$emit('close')

        },
        // onKeyUp: function (data) {
        //     debugger;
        //     const emailRegex = /^(\s?[^\s,]+@[^\s,]+\.[^\s,]+\s?,)*(\s?[^\s,]+@[^\s,]+\.[^\s,]+)$/;   
        //     const isValidEmail = emailRegex.test(data.value);
        //     if(!isValidEmail && data.error==''){
        //         const error=this.$t('EnterValidEmailAddress');
        //         debugger;
        //        return data.fieldInfo.error=error;             
        //     }else{
        //         data.fieldInfo.error='';
        //     }            
        // },

        onSubmit: function (formfields) {
            var vm = this;
            var objData = {
                user_email_id: (vm.Items.emailId > 0) ? vm.Items.emailId : 0,
                Userid: vm.Userid,
                typeId: formfields.CommunicationType,
                ScreenName: formfields.ScreenName,
                Description: formfields.Description,
                is_visible: formfields.is_visible,
                addToDir: true,
                is_default: false
            }
            var formData = new FormData();
            formData.append("postString", JSON.stringify(objData));
            DataService.AddUpdateEmail(formData).then(response => {
                if (response.data != null) {
                    const Data = JSON.parse(response.data);
                    if (Data.status_id == 1 && Data.status_msg == 'add') {
                        vm.ShowAlert(vm.$t("AddCommunication"), "success", vm.$t('Alert'));
                        vm.closePopup();
                        vm.$parent.FetchCommunication();
                        vm.isLoading = false;
                    } else if (Data.status_id == 2 && Data.status_msg == 'updated') {
                        vm.ShowAlert(vm.$t("UpdatedCommunication"), "success", vm.$t('Alert'));
                        vm.closePopup();
                        vm.$parent.FetchCommunication();
                        vm.isLoading = false;
                    } else {
                        vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                        vm.closePopup();
                        vm.isLoading = false;
                    }
                }
            });
        }

    },

});
</script>
