<template lang="en">
<div>
    <div class="my-popups">
        <div class="modal d-block ">

            <div class="modal-dialog ui-draggable" style="display: inline-block;">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle">
                        <h5 id="ui-id-26" class="modal-title">{{$t('Address')}}</h5>
                        <button type="button" title="" @click="$emit('close')" class="close" data-original-title="Close">
                            <i class="fa fa-times" aria-hidden="true"></i>
                        </button>
                        <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG" role="button"
                                aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('AddressGuide')"></div>
                                </div>
                            </div>
                        </span>
                    </div>
                    <div id="backgroundPopup" class="modal-body" style="height: auto; min-height: 0px; max-height: none;">
                        <section id="main-content" class="main-content">
                            <section class="main-content">
                                <div class="container-fluid p-0">
                                    <div class="p-0">
                                        <dynamic-form ref="addAddress" class="daterangecontrolHS" lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                                        </dynamic-form>
                                    </div>
                                </div>
                            </section>
                        </section>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<style>
.singleselectlook .multiselect__select::before {
    z-index: 99;
}

.singleselectlook .multiselect__tags {
    padding: 8px 30px 0 8px !important;
    min-height: 36px !important;
}

.singleselectlook .multiselect__input {
    margin-bottom: 0px;
}
</style>

<script>
import { defineComponent} from '@vue/composition-api'
import { extend,
    ValidationObserver,
    ValidationProvider
} from 'vee-validate';
import {
    ConvertCustomFieldIntoDynamicFormObj, ConvertDynamicFormObjToCustomFieldObj
} from "@/assets/common/vue-common.js"
import DataService from '../../services/DataService';
extend('required', {
    validate(value) {
        console.log("checkk")
        return {
            required: true,
            valid: ['', null, undefined].indexOf(value) === -1
        };
    },
    message: 'The {_field_} is required'
    ///computesRequired: true
});

export default defineComponent({
    name: "Manage Departemts",
    components: {
        ValidationObserver,
        ValidationProvider
    },
    props: {
        Userid: {
            type: Number
        },
        Items: {
            type: Array
        },
        addApiUrl: {
            type: String
        },
        emitName: {
            type: String
        },
        ModuleName: {
            type: String
        },
        SubModule: {
            type: String
        },
        title: {
            type: String
        },
        recordId: {
            type: Number
        }
    },
    data() {
        return {
            editor: this.CLASSIC_EDITOR,
            department: {
                name: '',
                status: {
                    name: "active",
                    id: 1
                },
                visibaleToclients: false,
                visibaleToTicting: false,
                description: '',
            },
            FormSchema: [{
                layoutType: "double",
                Data: [{
                        astype: "TextField",
                        label: this.$t('AddressLine1'),
                        name: "AddressLine1",
                        value: "",
                        placeholder: "Address Line 1",
                        validationRules: {
                            "required": true
                        },
                        disabled: false,
                    },
                    {
                        astype: "TextField",
                        label: this.$t('AddressLine2'),
                        name: "AddressLine2",
                        value: "",
                        placeholder: "Address Line 2",
                        validationRules: {
                            "required": false
                        },
                        disabled: false,
                    },
                    // {
                    //     astype: "SelectField",
                    //     label: this.$t('Country'),
                    //     name: "Country",
                    //     value: "",
                    //     placeholder: "Country",
                    //     config: {
                    //         options: [],
                    //         onChange: this.GetSubCategoryDDL
                    //     },
                    //     validationRules: {
                    //         "required": true
                    //     },
                    // },
                    {
                        astype: "SelectField",
                        label: this.$t('Country'),
                        name: "Country",
                        value: "",
                        placeholder: "",
                        validationRules: {
                            "required": true,
                            max: 250
                        },
                        config: {
                            option_request_url: process.env.VUE_APP_COMMON_API + '/Common/GetDDLData?moduleName=CRM&type=COUNTRIES',
                            token: this.GetUserInfo.Token,
                            onChange: this.onChangeDropdown
                        },
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('State/Province'),
                        name: "State",
                        value: "",
                        placeholder: "",
                        config: {
                            options: [],

                        },
                    },
                    // {
                    //     astype: "TextField",
                    //     label: this.$t('State/Province'),
                    //     name: "State/Province",
                    //     value: "",
                    //     placeholder: "State/Province",
                    //     validationRules: {
                    //         "required": false
                    //     },
                    //     disabled: false,
                    // },                            
                    {
                        astype: "TextField",
                        label: this.$t('City'),
                        name: "City",
                        value: "",
                        placeholder: "City",
                        validationRules: {
                            "required": false
                        },
                        disabled: false,
                    },
                    {
                        astype: "TextField",
                        label: this.$t('ZipCode'),
                        name: "ZipCode",
                        value: "",
                        placeholder: "Zip Code",
                        validationRules: {
                            "required": false
                        },
                        disabled: false,
                    },
                    {
                        astype: "TextField",
                        label: this.$t('Note/Description About Address'),
                        name: "NoteDescription",
                        value: "",
                        placeholder: "Note/Description About Address",
                        validationRules: {
                            "required": false
                        },
                        disabled: false,
                    },
                    {
                        astype: "toggleField",
                        label: this.$t('Visible To Client'),
                        name: "VisibleToClient",
                        value: "",
                        placeholder: "",
                        validationRules: "",
                    },

                ],
            }, ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closePopup,
                },
            ],
            // FormSchema: [],
            isLoading: false,
            StateData: []
        }

    },
    created: function () {
        var vm = this;       
        if (vm.Items.addressId > 0) {
            vm.FormSchema[0].Data[0].value = vm.Items.userAddress1;
            vm.FormSchema[0].Data[1].value = vm.Items.userAddress2;
            vm.FormSchema[0].Data[2].value = vm.Items.countryId;
            vm.onChangeDropdown(vm.Items.countryId);
            setTimeout(() => {
                vm.FormSchema[0].Data[3].value = vm.Items.userStateId;
            }, 300);           
            vm.FormSchema[0].Data[4].value = vm.Items.userCity;
            vm.FormSchema[0].Data[5].value = vm.Items.userZipCode;
            vm.FormSchema[0].Data[6].value = vm.Items.userAddressDesc;
            vm.FormSchema[0].Data[7].value = vm.Items.isVisibleToClient;
        }

        setTimeout(() => {
            this.$refs.addAddress.UpdateKeyValue();
        }, 200);
        // let id = this.recordId ? this.recordId : 0
        // this.getForm(id);

    },
    methods: {
        async onChangeDropdown(evt) {
            var vm = this;
            var countryId = typeof evt === 'object' ? evt.target.value : evt;
            var params = `countryId=${countryId}`
            await DataService.GetStateListByCountryId(params)
                .then(function (response) {
                    vm.StateData = JSON.parse(response.data).data;
                    vm.StateData = vm.StateData.map(item => {
                        return {
                            name: item.name,
                            value: item.state_id
                        }
                    });
                    $.each(vm.FormSchema, function (index, item) {
                        $.each(item.Data, function (index, element) {
                            if (element.name == 'State') {
                                element.value = '';
                                element.config.options = vm.StateData;
                            }
                        });
                    });
                });
        },
        onSubmit: function (formfields) {
            var vm = this;
            var objData = {
                user_address_id: vm.Items.addressId>0? vm.Items.addressId:0,
                Userid: vm.Userid,
                address1: formfields.AddressLine1,
                address2: formfields.AddressLine2,
                countryId: formfields.Country,
                stateId: formfields.State,
                stateName: (formfields.State > 0) ? vm.StateData.find(c => c.value === formfields.State).name : '',
                city: formfields.City,
                zipcode: formfields.ZipCode,
                desc: formfields.NoteDescription,
                is_visible: formfields.VisibleToClient,
                addToDir: true,
                // countryId = formfields.countryId
            }
            var formData = new FormData();
            formData.append("postString", JSON.stringify(objData));
            DataService.AddUpdateAddress(formData).then(response => {
                if (response.data != null) {
                    const Data = JSON.parse(response.data);
                    if (Data.status_id == 1 && Data.status_msg == 'add') {
                        vm.ShowAlert(vm.$t("AddedAddress"), "success", vm.$t('Alert'));
                        vm.closePopup();
                        vm.$parent.FetchCommunication();
                        vm.isLoading = false;
                    } else if (Data.status_id == 2 && Data.status_msg == 'updated') {
                        vm.ShowAlert(vm.$t("UpdatedAddress"), "success", vm.$t('Alert'));
                        vm.closePopup();
                        vm.$parent.FetchCommunication();
                        vm.isLoading = false;
                    } else {
                        vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                        vm.closePopup();
                        vm.isLoading = false;
                    }
                }
            });
        },
        getForm: function (id) {
            var vm = this;
            vm.isLoading = true;
            
            var url = `recordId=${id}&moduleName=${vm.ModuleName}&subModuleCode=${vm.SubModule}`;
            DataService.GetDynamicForm(url).then(function (response) {
                if (response != null && response.data != null) {

                    vm.isMapFeature = response.data.isFsmLocating;
                    response.data.data
                    vm.FormSchema = ConvertCustomFieldIntoDynamicFormObj(response.data.data)
                    console.log("tis", vm.FormSchema)
                    if (vm.FormSchema.length > 0) {
                        vm.FormSchema[0].visibility = false
                    }
                    vm.isLoading = false;
                }
            });
        },

        closePopup() {
            this.$emit('close')

        },

    },

});
</script>
